.movie {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  color: #fff;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  /* background-color: #f4f4f4; */
  /* border: 1px solid red; */
}
.movie:hover {
  background-color: rgba(83, 83, 83, 0.589);
  /* color: rgba(255, 255, 255, 0.842); */
}
.movie-center {
  position: relative;
  flex: 0 0 50%;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  cursor: pointer;
  /* border: 1px solid yellow; */
}
.movie-img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.movie-footer {
  flex: 1;
  display: flex;
  flex-direction: column;
  /* border: 1px solid blue; */
}
.movie-footer h3 {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1rem;
  padding: 0.25rem;
  overflow-y: auto;
  /* border: 1px solid red; */
}
.footer {
  flex: 0 0 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  /* border: 1px solid blue; */
}

.btn-details {
  flex: 0 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #f4f4f4;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  font-size: 1rem;
  cursor: pointer;
  /* border: 1px solid green; */
}

.movie-img {
  position: absolute;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 479px) {
  .movie-footer h3 {
    font-size: 1rem;
  }
  .footer {
    flex: 0 0 60px;
    font-size: 1rem;
  }
  .btn-details {
    flex: 0 0 40px;
    font-size: 1rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .movie-footer h3 {
    font-size: 1.05rem;
  }
  .footer {
    flex: 0 0 60px;
    font-size: 1.05rem;
  }
  .btn-details {
    flex: 0 0 40px;
    font-size: 1.05rem;
  }
}

@media screen and (min-width: 768px) {
  .movie-footer h3 {
    font-size: 1.1rem;
  }
  .footer {
    flex: 0 0 65px;
    font-size: 1.1rem;
  }
  .btn-details {
    flex: 0 0 40px;
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1024px) {
  .movie-footer h3 {
    font-size: 1.2rem;
  }
  .footer {
    flex: 0 0 70px;
    font-size: 1.2rem;
  }
  .btn-details {
    flex: 0 0 40px;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1280px) {
  .movie-footer h3 {
    font-size: 1.3rem;
  }
  .footer {
    flex: 0 0 75px;
    font-size: 1.3rem;
  }
  .btn-details {
    flex: 0 0 40px;
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 1440px) {
  .movie-footer h3 {
    font-size: 1.4rem;
  }
  .footer {
    flex: 0 0 80px;
    font-size: 1.4rem;
  }
  .btn-details {
    flex: 0 0 40px;
    font-size: 1.4rem;
  }
}
