.navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  border: 1px solid red;
  padding: 0.25rem;
}
.nav-center {
  width: 100%;
  /* height: 100%; */
  padding: 0.25rem;
}
.nav-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0.5rem;
}
.nav-toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.5rem;
  background-color: transparent;
  border-color: transparent;
  color: #fff;
  transition: all 0.3s linear;
  cursor: pointer;
}
.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  margin: 0 0.5rem;
  padding: 0.5rem;
  color: #f4f4f4;
}
.links {
  border: none;
}
.links a {
  display: block;
  padding: 0.4rem 1rem;
  font-size: 1rem;
  color: #f4f4f4;
  text-transform: capitalize;
  letter-spacing: 0.25rem;
  font-weight: bold;
}
.links a:hover {
  padding-left: 1.5rem;
  /* background-color: #f4f4f4; */
}
.links-container {
  height: 0;
  overflow: hidden;
  transition: all 0.3s linear;
}
@media screen and (max-width: 479px) {
  .navbar {
    flex: 0 0 90px;
  }
  .nav-toggle {
    font-size: 1.2rem;
  }
  .logo {
    font-size: 1.4rem;
  }
  .links {
    margin: 0 0.25rem;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 0.5rem;
  }
  .links a {
    padding: 0.25rem 1rem;
    font-size: 0.85rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .navbar {
    flex: 0 0 100px;
  }
  .nav-toggle {
    font-size: 1.25rem;
  }
  .logo {
    font-size: 1.45rem;
  }
  .links {
    margin: 0 0.25rem;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 0.5rem;
  }
  .links a {
    padding: 0.2rem 1rem;
    font-size: 0.9rem;
  }
}

@media screen and (min-width: 768px) {
  .navbar {
    flex: 0 0 110px;
  }
  .nav-center {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.25rem 1rem;
  }
  .nav-header {
    padding: 0;
  }
  .nav-toggle {
    display: none;
  }
  .logo {
    font-size: 1.5rem;
  }
  .links-container {
    height: auto !important;
  }
  .links {
    display: flex;
    align-items: center;
    margin-right: 0.5rem;
  }
  .links a {
    padding: 0;
    margin: 0.6rem;
    font-size: 1rem;
  }
  .links a:hover {
    padding: 0;
    background-color: transparent;
  }
}

@media screen and (min-width: 1024px) {
  .navbar {
    flex: 0 0 120px;
  }
  .logo {
    font-size: 1.6rem;
  }
  .links {
    margin-right: 0.6rem;
  }
  .links a {
    margin: 0.7rem;
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 1280px) {
  .navbar {
    flex: 0 0 130px;
  }
  .logo {
    font-size: 1.7rem;
  }
  .links {
    margin-right: 0.7rem;
  }
  .links a {
    /* margin: 0.7rem; */
    margin: 0.8rem;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1440px) {
  .navbar {
    flex: 0 0 140px;
  }
  .logo {
    font-size: 1.8rem;
  }
  .links {
    margin-right: 0.8rem;
  }

  .links a {
    margin: 0.9rem;
    font-size: 1.3rem;
  }
}
