.section-home {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 0.25rem;
  border: 1px solid red;
}
.index-container {
}
.prev-indexBtn,
.next-indexBtn {
  position: absolute;
  /* top: calc(100% - 70%); */
  /* transform: translateY(50%); */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  font-size: 1.3rem;
  background-color: transparent;
  border-color: transparent;
  color: #fff;
  opacity: 0.5;
  cursor: pointer;
}
.prev-indexBtn {
  left: 0;
  margin-left: 0.25rem;
  justify-content: flex-start;
}
.next-indexBtn {
  right: 0;
  margin-right: 0.25rem;
  justify-content: flex-end;
}
.prev-indexBtn:hover,
.next-indexBtn:hover {
  opacity: 1;
}
@media screen and (max-width: 479px) {
  .prev-indexBtn,
  .next-indexBtn {
    top: calc(100% - 75%);
    transform: translateY(25%);
    font-size: 3rem;
    /* padding: 0.5rem; */
  }
  .prev-indexBtn {
    margin-left: 1rem;
  }
  .next-indexBtn {
    margin-right: 1rem;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .prev-indexBtn,
  .next-indexBtn {
    top: calc(100% - 75%);
    transform: translateY(25%);
    font-size: 3.1rem;
  }
  .prev-indexBtn {
    margin-left: 1.5rem;
  }
  .next-indexBtn {
    margin-right: 1.5rem;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .prev-indexBtn,
  .next-indexBtn {
    top: calc(100% - 80%);
    transform: translateY(30%);
    font-size: 3.2rem;
  }
  .prev-indexBtn {
    margin-left: 2rem;
  }
  .next-indexBtn {
    margin-right: 2rem;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .prev-indexBtn,
  .next-indexBtn {
    top: calc(100% - 80%);
    transform: translateY(30%);
    font-size: 3.3rem;
  }
  .prev-indexBtn {
    margin-left: 2.5rem;
  }
  .next-indexBtn {
    margin-right: 2.5rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .prev-indexBtn,
  .next-indexBtn {
    top: calc(100% - 80%);
    transform: translateY(30%);

    font-size: 3.4rem;
  }
  .prev-indexBtn {
    margin-left: 3rem;
  }
  .next-indexBtn {
    margin-right: 3rem;
  }
}
@media screen and (min-width: 1440px) {
  .prev-indexBtn,
  .next-indexBtn {
    top: calc(100% - 80%);
    transform: translateY(30%);
    font-size: 3.5rem;
  }
  .prev-indexBtn {
    margin-left: 3.5rem;
  }
  .next-indexBtn {
    margin-right: 3.5rem;
  }
}
