.section-center {
  flex: 1;
  display: grid;
  grid-template-columns: 0.5fr 1.8fr 0.5fr;
  grid-template-rows: 80%;
  gap: 0.5rem;
  width: 100%;
  height: 100%;
  position: relative;
  border: 1px solid blue;
}
.home-article {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.25rem;
  /* border: 1px solid green; */
}
.img-center {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.activeSlide .img-center {
  cursor: pointer;
}
.img-center img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 0.25rem;
}
.activeSlide .home-title {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 20%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.5rem;
  color: #fff;
  /* border: 1px solid red; */
  overflow: hidden;
  padding: 0.25rem;
}
.lastSlide .home-title,
.nextSlide .home-title {
  display: none;
}
.hiddenSlide {
  display: none;
}

.lastSlide {
  order: 1;
  opacity: 0.4;
}
.activeSlide {
  order: 2;
  opacity: 1;
}
.nextSlide {
  order: 3;
  opacity: 0.4;
}

@media screen and (max-width: 479px) {
  .section-center {
    grid-template-columns: 0.7fr 2.5fr 0.7fr;
    grid-template-rows: 75%;
    gap: 0.5rem;
  }

  .activeSlide .home-title {
    height: 25%;
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .section-center {
    grid-template-columns: 0.7fr 2.6fr 0.7fr;
    grid-template-rows: 75%;
    gap: 0.5rem;
  }
  .activeSlide .home-title {
    height: 25%;
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 768px) {
  .section-center {
    grid-template-columns: 0.8fr 2.4fr 0.8fr;
    grid-template-rows: 70%;
    gap: 0.5rem;
  }
  .activeSlide .home-title {
    height: 30%;
    font-size: 1.7rem;
  }
}
@media screen and (min-width: 1024px) {
  .section-center {
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 70%;
  }
  .activeSlide .home-title {
    height: 30%;
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .section-center {
    grid-template-columns: 1fr 2.1fr 1fr;
    grid-template-rows: 70%;
  }
  .activeSlide .home-title {
    height: 30%;
    font-size: 1.9rem;
  }
}
@media screen and (min-width: 1440px) {
  .section-center {
    grid-template-columns: 1fr 2.2fr 1fr;
    grid-template-rows: 70%;
  }
  .activeSlide .home-title {
    height: 30%;
    font-size: 2rem;
  }
}
