.single-movie-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #000;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.5rem;
  transition: all 0.3s linear;
}
.single-movie-section:hover {
  border: 1px solid rgba(255, 255, 255, 1);
}
.single-title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.single-movie {
  flex: 1;
  border-radius: 0.25rem;
  overflow: auto;
}
.single-center {
  border-radius: 0.25rem;
}
.single-img {
  display: block;
  object-fit: fill;
  border-radius: 0.5rem;
}
.movie-info {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.movie-info::-webkit-scrollbar {
  display: none; /* Chrome , Safari , Opera */
}

.movie-info {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.5rem;
  overflow: auto;
}
.movie-info p {
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
}
.movie-data {
  font-weight: 100;
  text-transform: capitalize;
  color: #fff;
}

@media screen and (max-width: 479px) {
  .single-movie-section {
    margin-bottom: 0.3rem;
    padding: 0.3rem 0.3rem;
  }
  .single-title {
    padding: 0.4rem;
    margin: 0.25rem auto 0.4rem;
    text-align: center;
    font-size: 1.8rem;
  }
  .single-movie {
    display: flex;
    flex-direction: column;
  }
  .single-center {
    flex: 0 0 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.2rem 0.3rem;
  }
  .single-img {
    width: 100%;
    max-width: 500px;
    height: 300px;
    height: 250px;
    /* border: 1px solid red; */
  }
  .movie-info {
    flex: 1;
    padding: 0.25rem;
    margin: 0.3rem 0;
  }
  .movie-info p {
    padding: 0.1rem 0.2rem;
    margin-bottom: 0.25rem;
    font-size: 1.15rem;
    /* border: 1px solid red; */
  }
  .movie-data {
    font-size: 0.95rem;
    /* border: 1px solid red; */
  }
}

