.section-search {
  flex: 0 0 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  border: 1px solid red;
}
.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
  width: 90%;
  box-shadow: 2px 3px 2px 0 rgba(255, 255, 255, 0.4);
  background-color: #f2f2f2;
  border-radius: 0.25rem;
}
.form-control {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.form-control input {
  flex: 1;
  height: 100%;
  padding: 0 0.25rem;
}
.form-control input::placeholder {
  padding: 0.25rem;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
}

.search-btn {
  flex: 0 0 35px;
  /* height: 30px; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  color: #fff;
  background-color: #000;
  cursor: pointer;
  /* border: 1px solid green; */
}
.search-btn .icon {
  font-size: 1rem;
  padding: 0.2rem;
}
@media screen and (max-width: 479px) {
  .section-search {
    flex: 0 0 60px;
  }
  .search-form {
    /* width: 90%; */
  }
  .form-control input {
    /* padding: 0 0.25rem; */
  }
  .form-control input::placeholder {
    padding: 0.25rem;
    padding: 0.5rem;
    font-size: 0.85rem;
  }
  .search-btn {
    flex: 0 0 40px;
  }
  .search-btn .icon {
    font-size: 0.85rem;
    padding: 0.25rem;
  }
}
