.page-container {
  flex: 0 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 0.25rem; */
  border: 1px solid blue;
}
.page-btn {
  width: 100%;
  /* height: 100%; */
  display: flex;
  justify-content: space-between;
  border: 1px solid red;
}

.prev-btn,
.next-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.35rem;
  /* margin: 0 0.2rem; */
  background-color: transparent;
  border-color: transparent;
  text-transform: capitalize;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  border: 1px solid green;
}
.prev-btn:hover,
.next-btn:hover {
  color: #fff;
}
.prev-btn {
  margin-right: 0.2rem;
}
.next-btn {
  margin-left: 0.2rem;
}
.newPage-btn {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0.35rem;
  /* margin: 0 0.2rem; */
  border: 1px solid green;
}
.newPage {
  padding: 0.25rem;
  background-color: transparent;
  border-color: transparent;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  /* border: 1px solid red; */
}
.activePage {
  color: #fff;
}
@media screen and (max-width: 479px) {
  .page-container {
    flex: 0 0 50px;
  }
  .prev-btn,
  .next-btn {
    font-size: 0.85rem;
  }
  .prev-btn {
    margin-right: 0.2rem;
  }
  .next-btn {
    margin-left: 0.2rem;
  }
  .newPage {
    font-size: 0.85rem;
  }
}
